//	 @for $i from 1 through 3
//	   .item-#{$i}
//			width: 2em * $i;
//	 @each $s in a,b,c
// 			#{$s}
//				display: none

// ## fonts
// Save fonts to /css/fonts, after that write (eot, woff, ttf):
// +font(book, cytiapro-black-webfont)
// =book
// 	font-family: "book", Arial, sans-serif

@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,100,400italic,500,700,300italic&subset=latin,cyrillic);
@mixin f {
	font-family: Roboto, Arial, sans-serif; }
@mixin fi {
	font-family: "iconfont";
	font-style: normal;
	font-weight: normal;
	text-rendering: auto;
	speak: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@include font(GothamPro-Medium, GothamPro-Medium);
@mixin fg {
	font-family: "GothamPro-Medium", Roboto, Arial, sans-serif; }

$blue: #00acd3;
$red: #ff0303;
$cl: #353535;
$border: #e0e0e0;

body {
	@include f;
	font-size: 14px;
	line-height: 1.3;
	color: $cl; }
button,
input,
textarea {
	@include f;
	font-size: 14px; }
button {
	background: none; }
a {
	text-decoration: none;
	color: $blue; }

.center {
	max-width: 1250px;
	margin: 0 auto;
	padding: 0 50px;
	@include r(1023) {
		padding: 0 30px; }
	@include r(767) {
		padding: 0 20px; } }

.row {
	margin: 0 -15px;
	@include clr; }

[class*="col-"] {
	float: left;
	padding: 0 15px; }
.col-20 {
	width: 20%; }
.col-25 {
	width: 25%; }
.col-33 {
	width: 33.33%; }
.col-50 {
	width: 50%; }
.col-75 {
	width: 75%; }

.slick-slide img {
	display: inline-block; }
.slick-prev,
.slick-next {
	position: absolute;
	top: 50%;
	@include fi;
	font-size: 0;
	color: #fff;
	z-index: 2; }
.slick-prev {
	left: 50%;
	margin-left: -625px;
	@include r(1270) {
		left: 0;
		margin-left: 0; }
	@include r(1023) {
		left: -25px; }
	@include r(767) {
		left: -15px; }
	&:before {
		content: "\EA01"; } }
.slick-next {
	right: 50%;
	margin-right: -625px;
	@include r(1270) {
		right: 0;
		margin-right: 0; }
	@include r(1023) {
		right: -25px; }
	@include r(767) {
		right: -15px; }
	&:before {
		content: "\EA02"; } }
.slick-dots {
	padding-top: 30px;
	font-size: 0;
	white-space: nowrap;
	text-align: center;
	z-index: 2;
	li {
		display: inline-block;
		&.slick-active {
			button {
				&:before {
					transform: scale(2); } } } }
	button {
		width: 20px;
		height: 20px;
		font-size: 0;
		vertical-align: middle;
		&:before {
			content: "";
			display: inline-block;
			width: 6px;
			height: 6px;
			background: #FFF;
			border-radius: 50%;
			box-shadow: 0 0 2px rgba(#000,.5); } } }

.header {
	position: relative;
	height: 70px;
	background: #FFF;
	z-index: 10;
	@include r(1023) {
		height: 60px; }
	@include r(767) {
		height: 50px; }
	&__center {
		position: relative;
		@include clr;
		@include r(1023) {
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				height: 60px;
				background: #FFF;
				z-index: 2;
				@include r(767) {
					height: 50px; } } }
		@include r(767) {
			padding-left: 15px; } }
	&__logo {
		position: relative;
		float: left;
		width: 158px;
		height: 34px;
		margin: 20px 25px 0 0;
		background: url(../img/logo.png) no-repeat 0 0;
		background-size: cover;
		z-index: 4;
		@include r(1023) {
			margin: 13px 0 0 0; }
		@include r(767) {
			width: 130px;
			height: 28px;
			margin-top: 11px; } }
	&__btn-menu {
		display: none;
		position: absolute;
		top: 0;
		right: 0;
		width: 60px;
		height: 60px;
		font-size: 0;
		z-index: 4;
		@include r(1023) {
			display: block; }
		@include r(767) {
			width: 50px;
			height: 50px; }
		&.is-active {
			@include r(1023) {
				background: #446591;
				color: #FFF; }
			.icon-menu {
				@include r(1023) {
					display: none; } }
			.icon-close {
				@include r(1023) {
					display: inline-block; } } }
		.icon-menu {
			font-size: 12px; }
		.icon-close {
			display: none;
			font-size: 18px; } }
	&__menu {
		float: left;
		@include r(1270) {
			float: right; }
		@include r(1023) {
			position: absolute;
			top: 60px;
			left: 0;
			right: 0;
			height: 0;
			background: #fff;
			border-top: 1px solid #d9d9d9;
			box-shadow: 0 9px 30px rgba(0, 0, 0, 0.29);
			transition: all .4s;
			visibility: hidden;
			overflow: hidden; }
		@include r(767) {
			top: 50px; }
		&.is-active {
			@include r(1023) {
				height: 560px;
				visibility: visible; } } }
	&__time {
		float: right;
		margin-top: 17px;
		@include r(1270) {
			display: none; } } }

.menu {
	&__list {
		font-size: 0;
		@include r(1023) {
			display: flex;
			flex-flow: wrap;
			padding: 20px 10px 0; } }
	&__item {
		display: inline-block;
		vertical-align: top; }
	&__item:first-child {
		@include r(1023) {
			order: 5; } }
	&__item:first-child &__link {
		@include r(1023) {
			display: none; } }
	&__item:hover &__link_drop {
		background: #FFF;
		box-shadow: 0 9px 30px rgba(0, 0, 0, 0.29);
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #FFF;
			z-index: 2; } }
	&__item:hover &__drop {
		visibility: visible; }
	&__link {
		position: relative;
		display: table-cell;
		height: 70px;
		padding: 0 20px;
		vertical-align: middle;
		font-size: 0;
		color: #000;
		@include r(1023) {
			height: auto;
			padding: 10px 20px; }
		&:hover {
			background: #EEE;
			@include r(1023) {
				background: transparent; } }
		&_red {
			background: $red;
			color: #fff;
			@include r(1023) {
				background: transparent;
				color: #000; }
			&:hover {
				background: $red;
				@include r(1023) {
					background: transparent; } } } }
	&__icon,
	&__title {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		z-index: 4;
		.icon-menu {
			margin-right: 10px;
			padding-bottom: 2px;
			font-size: 12px; } }
	&__title {
		font-size: 16px;
		font-weight: 700;
		@include r(1023) {
			font-size: 20px;
			text-transform: uppercase; } }
	&__drop {
		position: absolute;
		top: 100%;
		left: 50px;
		right: 50px;
		min-height: 200px;
		background: #FFF;
		box-shadow: 0 9px 30px rgba(0, 0, 0, 0.29);
		visibility: hidden;
		@include r(1023) {
			position: relative;
			left: auto;
			right: auto;
			margin: 20px -10px 0;
			box-shadow: none;
			border-top: 1px solid #d9d9d9;
			background: #F9F9F9;
			text-align: left;
			visibility: visible; } }
	&__channels {
		display: table;
		width: 100%;
		padding: 30px 40px 25px 0;
		table-layout: fixed;
		@include r(1023) {
			display: block;
			padding: 30px; }
		&-col {
			display: table-cell;
			padding-left: 40px;
			border-left: 1px solid $border;
			vertical-align: top;
			@include r(1023) {
				display: block;
				margin-bottom: 30px;
				padding: 0;
				border: none; }
			&:first-child {
				border-color: transparent; }
			&:last-child {
				@include r(1023) {
					margin: 0; } } }
		&-col:nth-child(1) &-pic {
			height: 30px; }
		&-col:nth-child(2) &-pic {
			height: 23px; }
		&-col:nth-child(3) &-pic {
			height: 32px; }
		&-title {
			margin-bottom: 20px;
			font-size: 16px;
			text-transform: uppercase;
			font-weight: 700;
			line-height: 1;
			@include r(1023) {
				font-size: 18px; } }
		&-list {
			margin-bottom: -18px;
			columns: 2;
			@include r(1023) {
				columns: 3; } }
		&-link {
			display: inline-block;
			width: 100%;
			height: 32px;
			margin-bottom: 28px;
			@include vert; }
		&-pic {
			display: inline-block;
			max-height: 32px;
			vertical-align: middle; } }
	&__premieres {
		padding: 25px 40px;
		background: #F8F8F8;
		border-top: 1px solid $border;
		@include r(1023) {
			display: none; }
		&-head {
			margin-bottom: 20px;
			font-size: 16px;
			text-transform: uppercase;
			font-weight: 700;
			line-height: 1; }
		&-row {
			margin: 0 -20px;
			@include clr; }
		&-col {
			float: left;
			width: 16.66%;
			padding: 0 20px; }
		&-link {
			display: block; }
		&-preview {
			margin-bottom: 7px; }
		&-pic {
			display: block;
			max-width: 100%; }
		&-title {
			font-size: 13px;
			font-weight: 500;
			color: $cl; } } }

.time {
	&__cell {
		display: table-cell;
		vertical-align: middle;
		&:first-child {
			padding-right: 10px; } }
	&__icon {
		font-size: 0;
		color: #C0C0C0;
		.icon-clock {
			font-size: 29px; } }
	&__wrap {
		font-size: 0; }
	&__value,
	&__type {
		display: inline-block; }
	&__value {
		font-size: 16px;
		font-weight: 700;
		color: #000; }
	&__type {
		margin-left: 5px;
		font-size: 12px;
		color: #7D7D7D; }
	&__date {
		font-size: 12px;
		font-weight: 700; } }

.footer {
	padding: 40px 0 100px;
	@include r(1023) {
		padding: 40px 0; }
	@include r(767) {
		padding: 30px 0; }
	&__logo {
		display: block;
		width: 122px;
		height: 26px;
		margin-bottom: 19px;
		background: url(../img/logo.png) no-repeat 0 0;
		background-size: cover; }
	&__copyright {
		font-size: 12px;
		color: #888; }
	&__nav {
		&-link {
			display: table;
			margin-bottom: 5px;
			font-size: 14px;
			color: $cl;
			&:first-child {
				margin-bottom: 10px;
				font-weight: 700;
				text-transform: uppercase; } } }
	&__row {
		@include r(767) {
			display: flex;
			flex-flow: wrap; } }
	&__col {
		@include r(1023) {
			width: 30%;
			margin-bottom: 30px; }
		@include r(767) {
			width: 100%;
			margin-bottom: 20px; }
		&:first-child {
			@include r(767) {
				order: 4; } }
		&:last-child {
			margin-left: 30%;
			@include r(767) {
				margin-left: 0; } } } }

.promo {
	overflow: hidden;
	&__item {
		height: 520px;
		background-size: cover;
		background-position: 50% 50%;
		@include r(1023) {
			height: 400px; } }
	&__wrap {
		padding: 130px 0 0 70px;
		@include r(767) {
			padding: 170px 10px 0; } }
	&__channel {
		margin-bottom: 20px;
		@include r(1023) {
			margin-bottom: 10px; }
		&-pic {
			max-height: 50px;
			vertical-align: top;
			@include r(1023) {
				max-height: 46px; } } }
	&__info {
		display: table;
		padding: 20px 25px 22px;
		background: #D62228;
		color: #fff;
		text-transform: uppercase;
		line-height: 1;
		@include r(1023) {
			padding: 20px 20px 22px; } }
	&__title {
		margin-bottom: 5px;
		font-size: 42px;
		font-weight: 700;
		@include r(1023) {
			font-size: 34px; }
		@include r(767) {
			font-size: 24px; } }
	&__text {
		font-size: 24px;
		@include r(1023) {
			font-size: 18px; }
		@include r(767) {
			font-size: 14px; } }
	&__when {
		display: table;
		padding: 12px 25px;
		background: #fff;
		font-size: 0;
		font-weight: 700;
		color: #D62228;
		text-transform: uppercase;
		line-height: 1;
		@include r(1023) {
			padding: 12px 20px; } }
	&__date,
	&__time {
		display: inline-block;
		font-size: 13px;
		@include r(1023) {
			font-size: 12px; } }
	&__date {
		margin-right: 10px;
		padding-right: 10px;
		border-right: 2px solid; }
	.slick-prev,
	.slick-next {
		width: 50px;
		height: 100px;
		margin-top: -50px;
		@include r(767) {
			width: 20px;
			height: 40px; }
		&:before {
			font-size: 38px;
			text-shadow: 0 0 1px rgba(#000,.7);
			@include r(767) {
				font-size: 24px; } }
		&:hover {
			background: #161F2D; } }
	.slick-prev {
		@include r(1023) {
			left: 0; }
		&:before {
			content: "\EA09"; } }
	.slick-next {
		@include r(1023) {
			right: 0; }
		&:before {
			content: "\EA08"; } }
	.slick-dots {
		position: absolute;
		left: 50%;
		bottom: 35px;
		padding: 0;
		transform: translateX(-50%);
		@include r(1023) {
			bottom: 15px; } } }

.title {
	margin-bottom: 25px;
	@include fg;
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	@include r(1023) {
		font-size: 30px; }
	@include r(767) {
		font-size: 26px; } }

.search {
	padding: 25px 0;
	background: url(../img/bg-search.jpg) no-repeat 50% 50%;
	background-size: cover;
	color: #FFF;
	@include r(1023) {
		padding: 30px 0; }
	&__wrap {
		max-width: 620px;
		margin: 0 auto;
		text-align: center;
		@include r(1023) {
			max-width: 520px; } }
	&__title {
		margin-bottom: 15px; }
	&__form {
		position: relative;
		margin-bottom: 20px;
		border-radius: 3px;
		background: #FFF; }
	&__input {
		position: relative;
		width: 100%;
		height: 50px;
		padding: 0 15px 0 50px;
		background: none;
		font-size: 20px;
		z-index: 2; }
	&__icon {
		position: absolute;
		top: 16px;
		left: 16px;
		font-size: 0;
		color: $cl;
		.icon-search {
			font-size: 18px; } }
	&__text {
		max-width: 440px;
		margin: 0 auto;
		font-size: 16px;
		font-weight: 300; } }

.section {
	padding: 40px 0 50px;
	&_blue {
		background: #1773c3;
		color: #FFF;
		@include r(767) {
			padding-bottom: 40px; } }
	&_banners {
		padding-bottom: 40px;
		background: #e5e5e6; }
	&_slider {
		padding-bottom: 15px;
		background: #f2f2f3; } }

.blog {
	&__row {
		@include r(1023) {
			margin: 0 5px; }
		@include r(767) {
			margin: 0 -5px; } }
	&__group {
		padding: 0 15px; }
	&__head {
		margin-bottom: 10px;
		font-size: 0;
		&-lines,
		&-title {
			display: inline-block;
			font-size: 14px; }
		&-lines {
			margin-right: 5px;
			letter-spacing: -2px; }
		&-title {
			text-transform: uppercase;
			font-weight: 700; } }
	&__preview {
		margin-bottom: 10px; }
	&__pic {
		display: block;
		width: 100%; }
	&__link {
		display: block;
		position: relative;
		margin-bottom: 10px;
		padding-bottom: 6px;
		color: $cl;
		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 50px;
			height: 1px;
			background: $border; }
		&:hover {
			color: $blue; }
		&:first-child {
			font-size: 16px;
			font-weight: 700; }
		&:last-child {
			margin: 0;
			padding: 0;
			&:before {
				display: none; } } }
	&__more {
		padding-top: 30px; }
	.slick-prev,
	.slick-next {
		width: 30px;
		height: 44px;
		margin-top: -22px;
		transform: translateY(-130%);
		color: $cl;
		&:before {
			font-size: 20px; } } }

.more {
	text-align: center;
	&__btn {
		height: 42px;
		padding: 0 20px;
		border: 2px solid #adafb1;
		border-radius: 3px;
		font-size: 0; }
	&__icon,
	&__title {
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		padding: 2px 15px 0 0;
		font-size: 12px; }
	&__title {
		font-size: 14px;
		text-transform: uppercase;
		font-weight: 700; } }

.label {
	position: absolute;
	top: 15px;
	left: 0;
	padding: 10px 20px 10px 10px;
	font-size: 12px;
	overflow: hidden;
	&__cover {
		position: absolute;
		top: 0;
		left: -10px;
		right: 5px;
		bottom: 0;
		transform: skew(-15deg);
		opacity: .6; }
	&__title {
		position: relative;
		z-index: 2; } }

.premieres {
	&__row {
		@include r(1023) {
			margin: 0 5px; }
		@include r(767) {
			margin: 0 -5px; } }
	&__group {
		margin-bottom: 30px; }
	&__head {
		margin-bottom: 25px;
		&-title {
			margin-bottom: 5px;
			font-size: 30px;
			font-weight: 700;
			color: #000;
			@include r(1023) {
				margin-bottom: 10px; }
			@include r(767) {
				font-size: 24px; } }
		&-text {
			font-size: 16px;
			font-weight: 300; }
		&-col {
			float: right;
			@include r(1023) {
				position: relative; }
			&:first-child {
				@include r(1023) {
					width: 50%;
					z-index: 2; }
				@include r(767) {
					display: none; } }
			&:last-child {
				@include r(1023) {
					width: 100%;
					margin-top: -35px; }
				@include r(767) {
					margin-top: 0; } } } }
	&__detail {
		display: table;
		float: right;
		margin-top: 44px;
		font-size: 0;
		@include r(1023) {
			margin-top: 0; }
		&-title,
		&-icon {
			display: table-cell;
			vertical-align: middle; }
		&-title {
			font-size: 16px;
			font-weight: 700; }
		&-icon {
			padding-left: 10px;
			.icon {
				font-size: 16px; } } }
	&__item {
		padding: 0 15px; }
	&__link {
		display: block;
		color: #fff; }
	&__preview {
		position: relative;
		margin-bottom: 10px; }
	&__pic {
		display: block;
		width: 100%; }
	&__when {
		position: absolute;
		right: 0;
		bottom: 0;
		padding: 15px;
		text-shadow: 0 0 1px rgba(#000,.7);
		text-align: center; }
	&__time {
		font-size: 24px;
		font-weight: 700;
		line-height: 1.1; }
	&__date {
		font-size: 12px; }
	&__title {
		font-size: 16px;
		font-weight: 700;
		@include r(767) {
			font-size: 18px; } }
	&__text {
		font-size: 13px;
		font-style: italic;
		@include r(767) {
			font-size: 14px; } }
	&__grid-col {
		@include r(1023) {
			width: 50%; }
		@include r(767) {
			width: 100%; }
		&:first-child {
			@include r(1023) {
				width: 100%; } }
		&:nth-child(4) {
			clear: both; } }
	&__grid-col:first-child &__link {
		@include r(767) {
			padding-bottom: 10px; } }
	&__grid-col:not(:first-child) &__link {
		@include r(767) {
			padding: 10px 0;
			border-top: 1px solid #E1E1E1; } }
	&__grid-col:not(:first-child) &__preview {
		@include r(767) {
			float: left;
			width: 60px;
			padding-right: 10px; } }
	&__grid-col:not(:first-child) &__label {
		display: none; }
	&__grid-col:not(:first-child) &__pic {
		display: none; }
	&__grid-col:not(:first-child) &__when {
		position: relative;
		padding: 0;
		color: $cl;
		text-align: left;
		text-shadow: none; }
	&__grid-col:not(:first-child) &__time {
		font-size: 16px; }
	&__grid-col:not(:first-child) &__date {
		font-size: 12px;
		@include text-overflow; }
	&__grid-col:not(:first-child) &__content {
		display: table; }
	&__grid-col:not(:first-child) &__title {
		font-size: 14px; }
	&__grid-col:not(:first-child) &__text {
		font-size: 12px; }
	&__grid-more {
		display: none;
		padding: 15px 20px;
		background: #F3F3F3;
		font-size: 16px;
		font-weight: 700;
		text-align: center;
		@include r(767) {
			display: block; } }
	&__grid &__link {
		@include r(1023) {
			margin-bottom: 35px; }
		@include r(767) {
			margin: 0; } }
	&__grid &__label {
		padding: 7px 15px 7px 10px; }
	&__grid &__content {
		min-height: 80px;
		padding-bottom: 10px;
		@include r(1023) {
			min-height: 0;
			padding-bottom: 0; } }
	&__grid &__title {
		color: $cl; }
	&__grid &__text {
		color: #7F7F7F; }
	.slick-prev,
	.slick-next {
		width: 30px;
		height: 44px;
		margin-top: -22px;
		transform: translateY(-130%);
		color: #FFF;
		&:before {
			font-size: 20px; }
		&:hover {
			background: #FFF;
			color: #1773c3; } } }

